import React, { useState, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { X, Trophy, Calendar, Target, Flag } from 'lucide-react';
import { goalsAPI } from '../services/api';
import toast from 'react-hot-toast';

const EditGoalModal = ({ goal, onClose, onSuccess }) => {
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [selectedIcon, setSelectedIcon] = useState(goal?.icon || '🎯');
  const [selectedColor, setSelectedColor] = useState(goal?.color || '#10B981');

  const {
    register,
    handleSubmit,
    formState: { errors },
    watch,
    setValue,
    reset
  } = useForm({
    defaultValues: {
      title: goal?.title || '',
      description: goal?.description || '',
      category: goal?.category || 'personal',
      type: goal?.type || 'target',
      targetValue: goal?.targetValue || 1,
      currentValue: goal?.currentValue || 0,
      unit: goal?.unit || 'times',
      priority: goal?.priority || 'medium',
      deadline: goal?.deadline ? new Date(goal.deadline).toISOString().split('T')[0] : '',
      isPublic: goal?.isPublic || false
    }
  });

  const goalType = watch('type', 'target');
  const priority = watch('priority', 'medium');

  const icons = [
    '🎯', '🏆', '💪', '📚', '💰', '❤️', '🌟', '🚀',
    '⚡', '🔥', '💎', '🎨', '🎵', '🍎', '☀️', '🌱'
  ];

  const colors = [
    '#10B981', '#3B82F6', '#F59E0B', '#EF4444', '#8B5CF6',
    '#06B6D4', '#84CC16', '#F97316', '#EC4899', '#6B7280'
  ];

  const categories = [
    { value: 'health', label: 'Health', icon: '🏥' },
    { value: 'fitness', label: 'Fitness', icon: '💪' },
    { value: 'career', label: 'Career', icon: '💼' },
    { value: 'education', label: 'Education', icon: '📚' },
    { value: 'personal', label: 'Personal', icon: '🌟' },
    { value: 'financial', label: 'Financial', icon: '💰' },
    { value: 'social', label: 'Social', icon: '👥' },
    { value: 'other', label: 'Other', icon: '🎯' }
  ];

  const goalTypes = [
    { value: 'target', label: 'Target Goal', description: 'Achieve a specific target value' },
    { value: 'milestone', label: 'Milestone Goal', description: 'Complete a series of milestones' },
    { value: 'habit', label: 'Habit Goal', description: 'Build a consistent habit' }
  ];

  const priorities = [
    { value: 'low', label: 'Low', color: 'text-green-600' },
    { value: 'medium', label: 'Medium', color: 'text-blue-600' },
    { value: 'high', label: 'High', color: 'text-orange-600' },
    { value: 'urgent', label: 'Urgent', color: 'text-red-600' }
  ];

  useEffect(() => {
    if (goal) {
      reset({
        title: goal.title || '',
        description: goal.description || '',
        category: goal.category || 'personal',
        type: goal.type || 'target',
        targetValue: goal.targetValue || 1,
        currentValue: goal.currentValue || 0,
        unit: goal.unit || 'times',
        priority: goal.priority || 'medium',
        deadline: goal.deadline ? new Date(goal.deadline).toISOString().split('T')[0] : '',
        isPublic: goal.isPublic || false
      });
      setSelectedIcon(goal.icon || '🎯');
      setSelectedColor(goal.color || '#10B981');
    }
  }, [goal, reset]);

  const onSubmit = async (data) => {
    setIsSubmitting(true);
    try {
      const goalData = {
        ...data,
        icon: selectedIcon,
        color: selectedColor,
        milestones: data.milestones || []
      };

      await goalsAPI.updateGoal(goal._id, goalData);
      toast.success('Goal updated successfully! 🎉');
      onSuccess();
    } catch (error) {
      toast.error(error.response?.data?.message || 'Failed to update goal');
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="fixed inset-0 z-50 overflow-y-auto">
      <div className="flex items-center justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
        <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" onClick={onClose} />
        
        <div className="inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-2xl sm:w-full">
          <form onSubmit={handleSubmit(onSubmit)}>
            <div className="bg-white px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
              <div className="flex items-center justify-between mb-6">
                <h3 className="text-lg font-medium text-gray-900 flex items-center">
                  <Trophy className="h-5 w-5 mr-2 text-yellow-500" />
                  Edit Goal
                </h3>
                <button
                  type="button"
                  onClick={onClose}
                  className="text-gray-400 hover:text-gray-600"
                >
                  <X className="h-6 w-6" />
                </button>
              </div>

              <div className="space-y-6">
                {/* Goal Title */}
                <div>
                  <label className="label">Goal Title *</label>
                  <input
                    {...register('title', { required: 'Goal title is required' })}
                    type="text"
                    className="input"
                    placeholder="e.g., Learn Spanish"
                  />
                  {errors.title && <p className="text-red-500 text-xs mt-1">{errors.title.message}</p>}
                </div>

                {/* Description */}
                <div>
                  <label className="label">Description</label>
                  <textarea
                    {...register('description')}
                    rows={3}
                    className="input"
                    placeholder="Describe your goal..."
                  />
                </div>

                {/* Category */}
                <div>
                  <label className="label">Category *</label>
                  <select
                    {...register('category', { required: 'Category is required' })}
                    className="input"
                  >
                    {categories.map(category => (
                      <option key={category.value} value={category.value}>
                        {category.icon} {category.label}
                      </option>
                    ))}
                  </select>
                  {errors.category && <p className="text-red-500 text-xs mt-1">{errors.category.message}</p>}
                </div>

                {/* Goal Type */}
                <div>
                  <label className="label">Goal Type *</label>
                  <div className="grid grid-cols-1 sm:grid-cols-3 gap-3">
                    {goalTypes.map((type) => (
                      <label key={type.value} className="relative">
                        <input
                          {...register('type')}
                          type="radio"
                          value={type.value}
                          className="sr-only"
                        />
                        <div className={`p-3 border-2 rounded-lg cursor-pointer transition-all ${
                          goalType === type.value
                            ? 'border-primary-500 bg-primary-50'
                            : 'border-gray-200 hover:border-gray-300'
                        }`}>
                          <div className="font-medium text-gray-900">{type.label}</div>
                          <div className="text-sm text-gray-500 mt-1">{type.description}</div>
                        </div>
                      </label>
                    ))}
                  </div>
                  {errors.type && <p className="text-red-500 text-xs mt-1">{errors.type.message}</p>}
                </div>

                {/* Target Value */}
                <div>
                  <label className="label">Target Value *</label>
                  <div className="flex space-x-2">
                    <input
                      {...register('targetValue', {
                        valueAsNumber: true,
                        min: { value: 1, message: 'Target must be at least 1' }
                      })}
                      type="number"
                      className="input w-24"
                      placeholder="1"
                    />
                    <input
                      {...register('unit')}
                      type="text"
                      className="input flex-1"
                      placeholder="times, days, books, etc."
                    />
                  </div>
                  {errors.targetValue && <p className="text-red-500 text-xs mt-1">{errors.targetValue.message}</p>}
                </div>

                {/* Current Progress */}
                <div>
                  <label className="label">Current Progress</label>
                  <div className="flex space-x-2">
                    <input
                      {...register('currentValue', {
                        valueAsNumber: true,
                        min: { value: 0, message: 'Current value cannot be negative' }
                      })}
                      type="number"
                      className="input w-24"
                      placeholder="0"
                    />
                    <span className="flex items-center text-gray-500">/ {watch('targetValue') || 1} {watch('unit') || 'times'}</span>
                  </div>
                  {errors.currentValue && <p className="text-red-500 text-xs mt-1">{errors.currentValue.message}</p>}
                </div>

                {/* Priority */}
                <div>
                  <label className="label">Priority</label>
                  <div className="grid grid-cols-2 sm:grid-cols-4 gap-3">
                    {priorities.map((priorityOption) => (
                      <label key={priorityOption.value} className="relative">
                        <input
                          {...register('priority')}
                          type="radio"
                          value={priorityOption.value}
                          className="sr-only"
                        />
                        <div className={`p-3 border-2 rounded-lg cursor-pointer transition-all text-center ${
                          priority === priorityOption.value
                            ? 'border-primary-500 bg-primary-50'
                            : 'border-gray-200 hover:border-gray-300'
                        }`}>
                          <div className={`font-medium ${priorityOption.color}`}>{priorityOption.label}</div>
                        </div>
                      </label>
                    ))}
                  </div>
                </div>

                {/* Deadline */}
                <div>
                  <label className="label">Deadline (Optional)</label>
                  <input
                    {...register('deadline')}
                    type="date"
                    className="input"
                    min={new Date().toISOString().split('T')[0]}
                  />
                </div>

                {/* Icon Selection */}
                <div>
                  <label className="label">Icon</label>
                  <div className="grid grid-cols-8 gap-2">
                    {icons.map((icon) => (
                      <button
                        key={icon}
                        type="button"
                        onClick={() => setSelectedIcon(icon)}
                        className={`w-10 h-10 text-2xl rounded-lg border-2 flex items-center justify-center ${
                          selectedIcon === icon
                            ? 'border-primary-500 bg-primary-50'
                            : 'border-gray-200 hover:border-gray-300'
                        }`}
                      >
                        {icon}
                      </button>
                    ))}
                  </div>
                </div>

                {/* Color Selection */}
                <div>
                  <label className="label">Color</label>
                  <div className="flex space-x-2">
                    {colors.map((color) => (
                      <button
                        key={color}
                        type="button"
                        onClick={() => setSelectedColor(color)}
                        className={`w-8 h-8 rounded-full border-2 ${
                          selectedColor === color
                            ? 'border-gray-400'
                            : 'border-gray-200'
                        }`}
                        style={{ backgroundColor: color }}
                      />
                    ))}
                  </div>
                </div>

                {/* Public Goal */}
                <div className="flex items-center">
                  <input
                    {...register('isPublic')}
                    type="checkbox"
                    className="h-4 w-4 text-primary-600 focus:ring-primary-500 border-gray-300 rounded"
                  />
                  <label className="ml-2 text-sm text-gray-700">
                    Make this goal public (visible to others)
                  </label>
                </div>
              </div>
            </div>

            {/* Footer */}
            <div className="bg-gray-50 px-4 py-3 sm:px-6">
              <div className="flex flex-col sm:flex-row sm:justify-end sm:space-x-3 space-y-2 sm:space-y-0">
                <button
                  type="button"
                  onClick={onClose}
                  className="btn btn-outline btn-sm w-full sm:w-auto order-2 sm:order-1"
                >
                  Cancel
                </button>
                <button
                  type="submit"
                  disabled={isSubmitting}
                  className="btn btn-primary btn-sm w-full sm:w-auto order-1 sm:order-2 flex items-center justify-center"
                >
                  {isSubmitting ? (
                    <>
                      <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
                      Updating...
                    </>
                  ) : (
                    <>
                      <Trophy className="h-4 w-4 mr-2" />
                      Update Goal
                    </>
                  )}
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default EditGoalModal;
