import React, { useState } from 'react';
import { useForm } from 'react-hook-form';
import { X, Trophy, Calendar, Target, Flag } from 'lucide-react';
import { goalsAPI } from '../services/api';
import toast from 'react-hot-toast';

const CreateGoalModal = ({ onClose, onSuccess }) => {
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [selectedIcon, setSelectedIcon] = useState('🎯');
  const [selectedColor, setSelectedColor] = useState('#10B981');

  const {
    register,
    handleSubmit,
    formState: { errors },
    watch,
    setValue
  } = useForm();

  const goalType = watch('type', 'target');
  const priority = watch('priority', 'medium');

  const icons = [
    '🎯', '🏆', '💪', '📚', '💰', '❤️', '🌟', '🚀',
    '⚡', '🔥', '💎', '🎨', '🎵', '🍎', '☀️', '🌱'
  ];

  const colors = [
    '#10B981', '#3B82F6', '#F59E0B', '#EF4444', '#8B5CF6',
    '#06B6D4', '#84CC16', '#F97316', '#EC4899', '#6B7280'
  ];

  const categories = [
    { value: 'health', label: 'Health', icon: '🏥' },
    { value: 'fitness', label: 'Fitness', icon: '💪' },
    { value: 'career', label: 'Career', icon: '💼' },
    { value: 'education', label: 'Education', icon: '📚' },
    { value: 'personal', label: 'Personal', icon: '🌟' },
    { value: 'financial', label: 'Financial', icon: '💰' },
    { value: 'social', label: 'Social', icon: '👥' },
    { value: 'other', label: 'Other', icon: '🎯' }
  ];

  const goalTypes = [
    { value: 'target', label: 'Target Goal', description: 'Achieve a specific target value' },
    { value: 'milestone', label: 'Milestone Goal', description: 'Complete a series of milestones' },
    { value: 'habit', label: 'Habit Goal', description: 'Build a consistent habit' }
  ];

  const priorities = [
    { value: 'low', label: 'Low', color: 'text-gray-600' },
    { value: 'medium', label: 'Medium', color: 'text-blue-600' },
    { value: 'high', label: 'High', color: 'text-orange-600' },
    { value: 'urgent', label: 'Urgent', color: 'text-red-600' }
  ];

  const onSubmit = async (data) => {
    setIsSubmitting(true);
    try {
      const goalData = {
        ...data,
        icon: selectedIcon,
        color: selectedColor,
        milestones: data.milestones || []
      };

      await goalsAPI.createGoal(goalData);
      toast.success('Goal created successfully! 🎉');
      onSuccess();
    } catch (error) {
      toast.error(error.response?.data?.message || 'Failed to create goal');
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="fixed inset-0 z-50 overflow-y-auto">
      <div className="flex items-center justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
        <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" onClick={onClose} />
        
        <div className="inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-2xl sm:w-full">
          <form onSubmit={handleSubmit(onSubmit)}>
            <div className="bg-white px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
              <div className="flex items-center justify-between mb-6">
                <h3 className="text-lg font-medium text-gray-900 flex items-center">
                  <Trophy className="h-5 w-5 mr-2 text-yellow-500" />
                  Create New Goal
                </h3>
                <button
                  type="button"
                  onClick={onClose}
                  className="text-gray-400 hover:text-gray-600"
                >
                  <X className="h-6 w-6" />
                </button>
              </div>

              <div className="space-y-6">
                {/* Goal Title */}
                <div>
                  <label className="label">Goal Title *</label>
                  <input
                    {...register('title', { required: 'Goal title is required' })}
                    type="text"
                    className="input"
                    placeholder="e.g., Run a marathon"
                  />
                  {errors.title && (
                    <p className="mt-1 text-sm text-red-600">{errors.title.message}</p>
                  )}
                </div>

                {/* Description */}
                <div>
                  <label className="label">Description</label>
                  <textarea
                    {...register('description')}
                    rows={3}
                    className="input"
                    placeholder="Describe your goal and why it's important to you..."
                  />
                </div>

                {/* Category and Type */}
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <label className="label">Category *</label>
                    <select
                      {...register('category', { required: 'Category is required' })}
                      className="input"
                    >
                      {categories.map(category => (
                        <option key={category.value} value={category.value}>
                          {category.icon} {category.label}
                        </option>
                      ))}
                    </select>
                  </div>

                  <div>
                    <label className="label">Goal Type *</label>
                    <select
                      {...register('type', { required: 'Goal type is required' })}
                      className="input"
                    >
                      {goalTypes.map(type => (
                        <option key={type.value} value={type.value}>
                          {type.label}
                        </option>
                      ))}
                    </select>
                  </div>
                </div>

                {/* Target Value and Unit */}
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <label className="label">Target Value *</label>
                    <input
                      {...register('targetValue', { 
                        required: 'Target value is required',
                        valueAsNumber: true,
                        min: 1
                      })}
                      type="number"
                      min="1"
                      className="input"
                      placeholder="100"
                    />
                  </div>
                  <div>
                    <label className="label">Unit</label>
                    <input
                      {...register('unit')}
                      type="text"
                      className="input"
                      placeholder="miles, books, days..."
                    />
                  </div>
                </div>

                {/* Priority */}
                <div>
                  <label className="label">Priority</label>
                  <div className="grid grid-cols-4 gap-2">
                    {priorities.map(priorityOption => (
                      <label key={priorityOption.value} className="flex items-center">
                        <input
                          {...register('priority')}
                          type="radio"
                          value={priorityOption.value}
                          className="h-4 w-4 text-primary-600 focus:ring-primary-500 border-gray-300"
                        />
                        <span className={`ml-2 text-sm ${priorityOption.color}`}>
                          {priorityOption.label}
                        </span>
                      </label>
                    ))}
                  </div>
                </div>

                {/* Deadline */}
                <div>
                  <label className="label flex items-center">
                    <Calendar className="h-4 w-4 mr-1" />
                    Deadline (optional)
                  </label>
                  <input
                    {...register('deadline')}
                    type="date"
                    className="input"
                  />
                </div>

                {/* Icon Selection */}
                <div>
                  <label className="label">Choose Icon</label>
                  <div className="grid grid-cols-8 gap-2">
                    {icons.map(icon => (
                      <button
                        key={icon}
                        type="button"
                        onClick={() => setSelectedIcon(icon)}
                        className={`w-10 h-10 rounded-lg border-2 flex items-center justify-center text-lg ${
                          selectedIcon === icon
                            ? 'border-primary-500 bg-primary-50'
                            : 'border-gray-200 hover:border-gray-300'
                        }`}
                      >
                        {icon}
                      </button>
                    ))}
                  </div>
                </div>

                {/* Color Selection */}
                <div>
                  <label className="label">Choose Color</label>
                  <div className="flex space-x-2">
                    {colors.map(color => (
                      <button
                        key={color}
                        type="button"
                        onClick={() => setSelectedColor(color)}
                        className={`w-8 h-8 rounded-full border-2 ${
                          selectedColor === color
                            ? 'border-gray-400'
                            : 'border-gray-200'
                        }`}
                        style={{ backgroundColor: color }}
                      />
                    ))}
                  </div>
                </div>

                {/* Public Goal */}
                <div className="flex items-center">
                  <input
                    {...register('isPublic')}
                    type="checkbox"
                    className="h-4 w-4 text-primary-600 focus:ring-primary-500 border-gray-300 rounded"
                  />
                  <label className="ml-2 text-sm text-gray-700">
                    Make this goal public (friends can see your progress)
                  </label>
                </div>
              </div>
            </div>

            <div className="bg-gray-50 px-4 py-3 sm:px-6">
              <div className="flex flex-col sm:flex-row sm:justify-end sm:space-x-3 space-y-2 sm:space-y-0">
                <button
                  type="button"
                  onClick={onClose}
                  className="btn btn-outline btn-sm w-full sm:w-auto order-2 sm:order-1"
                >
                  Cancel
                </button>
                <button
                  type="submit"
                  disabled={isSubmitting}
                  className="btn btn-primary btn-sm w-full sm:w-auto order-1 sm:order-2"
                >
                  {isSubmitting ? 'Creating...' : 'Create Goal'}
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default CreateGoalModal;
